const Search = props => (
    <div>
      <div className="searchBox">
          <input type="text" className="searchBar" placeholder="Search"></input>
          
      </div>
  
      <style jsx>{`
        .searchBar{
          background-color: white;
          height: 20px;
          width: 100%;
          position: fixed;
          top: 20px;
        }
      `}
      </style>
    </div>
  );
  
  export default Search;